<template>
  <div class="container-fluid">
    <div class="column-flex mb-0 pb-0" style="background: #e3e5eb;">
      <Modal />
      <v-data-table
        :headers="headers"
        :items="desserts"
        :page.sync="page"
        :items-per-page="itemsPerPage"
        hide-default-footer
        @page-count="pageCount = $event"
      >
        <template v-slot:item.image="{ item }">
          <img class="img-alu" :src="item.image" height="60px" />
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
          <v-icon small @click="deleteItem(item)">delete</v-icon>
        </template>
      </v-data-table>
      <div class="text-center pt-2">
        <v-pagination v-model="page" :length="pageCount"></v-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from "./Modal";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Modal
  },
  data: () => ({
    page: 1,
    pageCount: 0,
    itemsPerPage: 10
  }),
  computed: {
    ...mapState(["desserts", "headers"])
  }
};
</script>

<style>
/* .lista {
  padding: 15px;
  width: 100%;
} */
.img-alu {
  margin: 10px;
  border-radius: 1em;
  box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.575);
}
</style>